

.background-friends {
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    width: 100vw;
    /* height: 100vh; */
    background-image: url('../../assets/images/FriendsBackround.png');
    background-size: cover; /* Покрывает весь элемент */
    background-position: center; /* Центрирует изображение */
    background-repeat: no-repeat; /* Не повторяет изображение */
    /* /* width: 430px; */
    height: 932px; 
  }


  .tabs-menu{
    display: flex;
    margin-top: 3vh;
    justify-content: center;
    gap: 8vw;
  }

  .tab-button {
    /* Friends */
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 110%;
    /* identical to box height, or 22px */
    text-align: center;

    color: rgba(255, 255, 255, 0.5);
    background: none;
    border: none;
    /* padding: 10px 20px;
    font-size: 16px; */
    cursor: pointer;
    outline: none;
  }
  
  /* .tab-button:hover {
    color: rgba(255, 255, 255, 0.5);
  } */
  
  .tab-button.active {
    color: #FFFFFF;
  }
  
  .tab-content {
    padding: 2vh;
    display: flex;
    justify-content: center;
    align-items: center; 
  }


  .tab-headline{
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 110%;
    /* identical to box height, or 53px */
    text-align: center;
    
    color: #ECE4CB;
  }


  .friend-content {
    display: flex;
    justify-content: center;
    align-items: center; 
    flex-direction: column;
  }

  .referral-container {
    display: flex;
    align-items: center; /* Вертикальное выравнивание элементов */
    margin-top: 20px;
    gap: 20px; /* Расстояние между полем и кнопкой */
  }
  
  .referral-input {
    flex-grow: 1; 
    /* padding: 10px; */
    font-size: 16px;
    cursor: pointer; /* Указатель при наведении */
    width: 66vw;
    height: 46px;
    background: #F3F3F3;
    border-radius: 10px;

  }
  
  .referral-input:focus {
    outline: none; /* Убираем обводку при фокусе */
    border-color: #007bff; /* Цвет рамки при фокусе */
  }
  
  .send-button {
    /* padding: 10px 20px; */
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer; /* Указатель при наведении */

    width: 19vw;
    height: 50px;

    background: #56ABE3;
    border-radius: 10px;

    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 110%;
    text-align: center;




  }
  
  .send-button:hover {
    background-color: #0056b3; /* Цвет кнопки при наведении */
  }

  .referral-alert{
    display: flex;
    justify-content: center;      
    align-items: center;
    background: #1974B1;
    mix-blend-mode: screen;
    backdrop-filter: blur(37.15px);
    border-radius: 15px;
    /* Каждый приглашенный тобою друг принесет дополнительных 25 монет, поспеши! */
    width: 70vw;
    height: 100px;
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 155%;
    /* or 25px */
    color: #FFFFFF;

    margin-top: 15px;
  }

  .referral-list{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    margin-top: 10px;
    width: 70vw;
    height: 348px;
    background: #1974B1;
    mix-blend-mode: normal;
    backdrop-filter: blur(37.15px);
    border-radius: 15px;
    margin-bottom: 15px;
  }

  .ref-text{
    width: 44vw;
    height: 37px;
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 110%;
    text-align: center;
    color: #FFFFFF;
    margin-bottom: 15px;
  }

  .ref-friend{
    width: 58vw;
    height: 245px;
    overflow-y: scroll;   
    padding-right: 15px; 
    overflow-x: hidden;
  }


.friend-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 58vw;
    height: 33px;
    margin-bottom: 10px;
    background: #F3F3F3;
    mix-blend-mode: normal;
    border-radius: 10px;
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 110%;
    text-align: left;

    color: #707070;
  }
  

  .friend-info {
    flex: 1;
    text-align: left;
    margin-left: 5px;
    padding-right: 10vw; /* Небольшой отступ справа */
    white-space: nowrap; /* Оставляет текст в одну строку */
    overflow: hidden;    /* Скрывает текст, если он выходит за границы */
    text-overflow: ellipsis; /* Добавляет троеточие, если текст слишком длинный */
  }
  
  .friend-right {
    display: flex;
    align-items: center;
    /* gap: px; Небольшой отступ между изображением и монетами */
  }

  .friend-image {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    width: 10px;
    height: 17px;
  }
  
  .friend-coin {
    font-family: 'Arial', sans-serif; /* Или любой другой шрифт */
    font-weight: bold;
    text-align: right;
    margin-right: 5px;
    flex: 1;
  }



  /* Стилизация скроллбара */
  .ref-friend::-webkit-scrollbar {
    margin-left: -15px;
    border-radius: 4px;  
    width: 8px;
  }
  
  .ref-friend::-webkit-scrollbar-thumb {
    background-color: #ffffff;      /* Цвет ползунка */
    border-radius: 4px;  
    /* width: 4px; */
  }
  
  .ref-friend::-webkit-scrollbar-thumb:hover {
    background-color: #ffffff;      /* Цвет ползунка при наведении */
  }
  
  .ref-friend::-webkit-scrollbar-track {
    background-color: #56ABE3;   /* Цвет фона скроллбара */
    border-radius: 4px; 
    width: 6px;
  }


  .leaderboards-list{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    margin-top: 10px;
    width: 70vw;
    height: 450px;
    background: #1974B1;
    mix-blend-mode: normal;
    backdrop-filter: blur(37.15px);
    border-radius: 15px;
    margin-bottom: 15px;
  }

  .leader-friend{
      width: 58vw;
      height: 350px;
      overflow-y: scroll;   
      padding-right: 15px; 
      overflow-x: hidden;
  }