.up-menu{
    display: flex;
    align-items: center;
    /* position: absolute; */
    /* width: 430px;
    height: 160px; */
    width: 100vw;
    height: 17vh;
    background: linear-gradient(90deg, #2C90D2 0%, #1873AF 100%);
    border: 1px solid #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 0px 0px 35px 35px;
}



.account-menu{
    display: flex;
    align-items: center;
    
    /* margin-top: 100px; */
    margin-top: 9.3vh;
    /* width: 434px;
    height: 60px; */
    width: 100vw;
    height: 6.3vh;
}

.user-icon {
    margin-left: 6.97vw;
    margin-bottom: 3.2vh;
  }


.vertical-text {
    display: flex;
    flex-direction: column;
    align-items: flex-start; /* Выравнивает текст по левому краю */
  }
  
.user-name {
    margin-bottom: 5px;
    margin-left: 10px;
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 110%;
    /* identical to box height, or 18px */
    color: #FFFFFF;
  }

.count-days{
    /* В семье XXX дней */

    /* position: absolute;
    width: 100px;
    height: 13px;
    left: calc(50% - 100px/2 - 91px);
    top: 115px; */
    margin-bottom: 30px;
    margin-left: 10px;
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 110%;
    /* or 13px */
    text-align: center;

    color: #FFFFFF;


}



.button-set{
    display: flex;
    position: absolute; 
    right: 20px; /* Отступ от правого края контейнера */
    /* margin-left: 60vw; */
    align-self: flex-start;
    margin-bottom: 30px;
    width: 50px;
    height: 32px;
    justify-content: center; /* Центрирует текст по горизонтали */
    align-items: center; /* Центрирует текст по вертикали */
    background: #A0C2EB;
    border-radius: 8px; 
    color: white;
    cursor: pointer;
    transition: background-color 0.3s ease;
    text-align: center;
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 110%;
    text-align: center;
}

.button-set:hover {
    background-color: #0056b3;
  }