.loading-screen {
  position: fixed;
  width: 100vw; /* Ширина экрана */
  height: 100vh; /* Высота экрана */
  top: 0;
  left: 0;
  background: linear-gradient(180deg, #161616 38.34%, #0172B8 71.33%, #2D7DC6 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: 'Alegreya SC';
  font-style: normal;
  font-weight: 500;
  font-size: 2.5vw; /* Используем vw для адаптивности */
  line-height: 110%;
  text-align: center;
  color: #FFFFFF;
}

.loading-lottie {
  margin-bottom: 3vh; /* Процент от высоты экрана */
}

.loading-text {
  font-family: 'Alegreya SC';
  font-weight: 800;
  font-size: 8vw; /* Используем vw для адаптивности */
  line-height: 110%;
  text-align: center;
  color: #FFFFFF;
  display: flex;
  align-items: center;
  margin-top: 3vh;
  justify-content: center;
}

.dots-container {
  display: flex;
  align-items: flex-end;
  margin-top: 1vh;
  margin-left: 0.5vw;
}

.dot {
  width: 1.5vw; /* Адаптивная ширина точек */
  height: 1.5vw; /* Адаптивная высота точек */
  margin: 0.5vw; /* Расстояние между точками */
  border-radius: 50%;
  background-color: #fbfbfb;
  animation: bounce 1.4s infinite ease-in-out both;
}

@keyframes bounce {
  0%, 80%, 100% {
      transform: translateY(0);
  }
  40% {
      transform: translateY(-1.5vw); /* Высота прыжка адаптируется */
  }
}

.dot:nth-child(2) {
  animation-delay: 0.2s;
}

.dot:nth-child(3) {
  animation-delay: 0.4s;
}
