.background-container {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    background-image: url('./MainWindow.jpg');
    background-size: cover; /* Покрывает весь элемент */
    background-position: center; /* Центрирует изображение */
    background-repeat: no-repeat; /* Не повторяет изображение */
    /* width: 430px;
    height: 932px; */
  }


.click-zone{
    display: flex;
    align-items: center;
    width: 79vw;
    height: 30vh;
    margin-left: 11vw;
    margin-top: 3.2vh;
}

.click-image {
    position: absolute;
    width: 56vw;
    height: 33vh;
    left: 23vw;
    top: 30vh;
    transition: transform 0.5s ease;
}


.animate {
  transform: scale(1.2); /* Увеличение до 1.5 раза */
}



.power-block{
    display: flex;
    align-items: center;
    /* margin-top: 220px;
    margin-left: 320px; */
    margin-top: 23vh;
    margin-left: 70vw;
    /* background-color: red; */
}

.power-text{
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 110%;
    /* or 22px */
    text-align: center;
    font-weight: bold;
    color: #FFFFFF;
}