.menu-bottom {
    display: flex; /* Горизонтальное расположение */
    align-items: center; /* Центрирование содержимого по вертикали */
    justify-content: space-between; /* Равномерное распределение кнопок */
    background-color: #1989D4; /* Фон меню */
    height: 70px;
    width: 88vw; /* Ширина меню */
    border-radius: 15px; /* Скругленные углы */
    margin: 20px auto; /* Отступы сверху и снизу, центрирование по горизонтали */
    padding: 0 2vw; /* Отступы по краям */
  }




  
  .menu-button{
    display: flex; /* Горизонтальное расположение */
    justify-content: center;  /*Центрирование кнопок*/
    align-items: center; /* Центрируем содержимое по вертикали */
    margin: 0px;
    /* margin-top: 9px; */
    /* margin-left: 5px; */
    /* margin-right: 5vw; */ 
    height: 52px;
    width: 66px;
  }

  .menu-button-items {
    display: flex;
    flex-direction: column; /* Иконка и текст расположены вертикально */
    transition: background-color 0.3s, transform 0.3s; 
    box-sizing: border-box; /* Включаем padding и border в ширину и высоту */
    align-items: center; /* Центрирование содержимого по горизонтали */
    text-decoration: none; /* Убираем подчеркивание */
    color: inherit; /* Наследуем цвет текста */
    /* height: 70px; */
    /* padding: 10px; Внутренний отступ */
    /* transition: background-color 0.3s, transform 0.3s; Плавная анимация на наведение */ 
  }
  
  .menu-button:hover {
    background-color: #2A7EB7; 
    border-radius: 8px; 
    transform: scale(1.02); 
  }
  
  .menu-button img {
    /* max-width: 100%; Иконка занимает всю ширину блока */
    height: auto; /* Сохраняем пропорции иконки  */
    margin-bottom: 8px; /* Отступ между иконкой и текстом */
  }
  
  .menu-button span {

        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 9px;
        line-height: 110%;
        /* identical to box height, or 10px */
        /* text-align: center; */
    
        color: #FFFFFF;
  }
  