.background-settings {
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    width: 100vw;
    height: 100vh;
    background-image: url('../../assets/images/SettingsBackgroung.png');
    background-size: cover; /* Покрывает весь элемент */
    background-position: center; /* Центрирует изображение */
    background-repeat: no-repeat; /* Не повторяет изображение */
    /* /* width: 430px; */
    /* height: 932px;  */
  }


.settings-text{
    margin-top: 210px;
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 110%;
    text-align: center;
    color: #ECE4CB;
}



/* Контейнер для всех переключателей */
.toggle-group {
    display: flex;
    margin-top: 15px;
    flex-direction: column;
    gap: 15px; /* Отступы между переключателями */
    margin-bottom: 25px;
  }
  
  /* Контейнер каждого переключателя */
  .toggle-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    width: 58vw; /* Ширина контейнера */
    
  }
  
  .toggle-label {
    font-size: 16px;
    margin-right: 20px; /* Отступ между текстом и переключателем */
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 110%;
    /* identical to box height, or 22px */
    text-align: center;
    color: #FFFFFF;


  }
  
/* Стили для переключателя */
.switch {
    position: relative;
    display: inline-block;
    width: 23vw; /* Адаптивная ширина */
    height: 30px;
  
    background: rgba(25, 116, 177, 0.5);
    mix-blend-mode: screen;
    backdrop-filter: blur(37.15px);
    border-radius: 15px;
  }
  
  /* Скрываем сам input */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* Ползунок (слайдер) */
  .slider {
    /* position: relative; */
    display: flex;
    cursor: pointer;
    border-radius: 15px;
    transition: 0.4s;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  /* Внутри слайдера текст ON и OFF */
  .slider-text {
    font-size: 14px;
    font-weight: bold;
    position: absolute;
    color: white;
    top: 50%;
    transform: translateY(-50%); /* Центрируем текст вертикально */
    width: 50%; /* Каждая часть текста занимает половину ширины слайдера */
    text-align: center;
    z-index: 1; /* Текст должен быть выше ползунка */
  }
  
  .slider-text.on {
    left: 0; /* Текст "ON" слева */
  }
  
  .slider-text.off {
    right: 0; /* Текст "OFF" справа */
  }
  
  
  /* Ползунок */
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 45%; /* Ползунок занимает 45% ширины */
    left: 2px;
    top: 50%;
    transform: translateY(-50%); /* Вертикально центрируем ползунок */
    background-color: white;
    transition: 0.4s;
    border-radius: 15px;
    background: #1974B1;
    mix-blend-mode: screen;
    backdrop-filter: blur(37.15px);
  }
  
  /* Анимация ползунка при checked */
  input:checked + .slider:before {
    transform: translateX(calc(23vw - 100% - 6px)) translateY(-50%);
  }
  
  /* Цвет текста "ON" при включении */
  input:checked + .slider .on {
    color: white;
    display: flex;
    justify-content: center;
  }

.connect-button{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 69vw;
    height: 30px;
    background: #1974B1;
    mix-blend-mode: screen;
    backdrop-filter: blur(37.15px);
    border-radius: 15px;

