    cursor: pointer; /* Указатель при наведении */


    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 110%;
    text-align: center;

    color: #FFFFFF;
    margin-bottom: 350px;

}