.stat-menu{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    /* background-color: red; */
}

.stat-block{
    background: #A0C2EB;
    border-radius: 15px;
    width: fit-content;
    height: 41px;
    margin-left: 15px;
    margin-right: 15px;
    display: flex;
    justify-content: start;
    align-items: center;
    padding-right: 6px;
}


.stat-icons{
    margin-left: 3px;
    margin-top: 5px;
}

.stat-coin{
    margin-left: 5px;
    margin-top: 2px;
}

.stat-text{
    /* 999999 */

    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 110%;
    /* or 26px */
    text-align: center;
    color: #FFFFFF;
}

